.main_body {
    display: flex;
    margin-top: 20px;
  }
  * {
    margin: 0;
    padding: 0;
  }
  .main{
    background-color: #f3f2ef;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
  }
.Sidebar {
  top: 80px;
  position: -webkit-sticky;
  position: sticky;
  flex: 0.2 1;
  border-radius: 10px;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  
    margin-top: 70px;
}
.sidebar__avatar {
  margin-bottom: 10px;
}
.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}
.sidebar__top > img {
  margin-bottom: -20px;
  width: 100%;
  height: 60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}
.sidebar__info {
  color: black;
  font-size: 10px;
}
.sidebar__stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.sidebar__stat > p {
  color: gray;
  font-size: 13px;
  font-weight: 600;
}
.sidebar__statnumber {
  font-weight: bold;
  color: #0a66c2 !important ;
}
.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border: 1px solid lightgray;
  background-color: white;
}
.sidebar__top > h4 {
  color: gray;
  font-size: 12px;
}
.sidebar__top > h2 {
  color: gray;
  font-size: 10px;
}
.sidebar__button {
  text-align: left;
  border: 1px solid lightgray;
  background-color: white;
  border-radius: 10px;
  margin-top: 10px;
}
.sidebar__recentitem {
  display: flex;
  font-size: 13px;
  color: gray;
  font-weight: bolder;
  cursor: pointer;
  margin-bottom: 5px;
  padding: 5px;
}
.sidebar__recentitem:hover {
  background-color: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
  color: black;
}
.sidebar__hash {
  margin-right: 10px;
  margin-left: 5px;
}

.Widgets {
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
  flex: 0.2 1;
  background-color: white;
  border-radius: 10px;
  border: 1px solid lightgray;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 10px;
}
.Widgets__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}
.Widgets__header > h2 {
  font-size: 16px;
  font-weight: 400;
}
.widgets__Article {
  display: flex;
  padding: 10px;
  cursor: pointer;
}
.widgets__Article:hover {
  background-color: whitesmoke;
}
.widgets__Articleleft {
  color: #0177b7;
  margin-right: 5px;
}
.widgets__Articleleft > .MuiSvgIcon-root {
  font-size: 15px;
}
.widgets__Articleright {
  flex: 1 1;
}
.widgets__Articleright > h4 {
  font-size: 14px;
}
.widgets__Articleright > p {
  font-size: 12px;
  color: gray;
}

.Feed {
  flex: 0.6 1;
  margin: 0 20px;
  margin-top: 20px;
}
.feed__inputContainer {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}
.feed__input {
  border: 1px solid lightgray;
  border-radius: 30px;
  display: flex;
  padding: 10px;
  color: gray;
  padding: 15px;
}
.feed__input > form {
  display: flex;
  width: 100%;
}
.feed__input > form > input {
  border: none;
  flex: 1 1;
  margin-left: 10px;
  outline-width: 0;
  font-weight: 600;
}
.feed__input > form > button {
  display: none;
}
.feed__inputOptionicons {
  display: flex;
  justify-content: space-evenly;
}

.InputOption {
  display: flex;
  align-items: center;
  margin-top: 15px;
  color: gray;
  padding: 10px;
  cursor: pointer;
}
.InputOption:hover {
  background-color: whitesmoke;
  border-radius: 10px;
}
.InputOption > h4 {
  margin-left: 5px;
}

a{
    color: black;
    text-decoration: none;
}

.Post {
    background-color: white;
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 10px;
  }
  .Post:hover{
    box-shadow: 0 8px 6px -6px #777;
  }
  .post__header {
    display: flex;
    margin-bottom: 10px;
  }
  .post__info {
    margin-left: 10px;
  }
  .post__info > p {
    font-size: 12px;
    color: gray;
  }
  .post__body {
    overflow-wrap: anywhere;
  }
  .post__body > p{
    font-size: 16px;
  }
  .post__info > h2 {
    font-size: 12px;
  }
  .post__buttons {
    display: flex;
    justify-content: space-evenly;
  }
  .post__tags {
      margin-top: 15px;
      display: flex;
      justify-content: flex-start;
  }
  .tag-ele {
      display: block;
      margin-right: 5px;
      /* border: 1px solid rgb(105, 105, 105);
      color: #404040; */
      border: 1px solid #c21808;
      color: #c21808;
      background-color: white;
      border-radius: 15px;
      font-size: 15px;
      /* font-weight: 450; */
      padding: 3px 10px;
  }
.answer-container {
    display: flex;
    justify-content: flex-start;
}
.vote-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 0px 10px;
}
.vote-count-container {
    width: 100%;
    text-align: center;
    font-weight: 400;
}
